







































































































































































































#mobileUserManagement {
  position: relative;
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    line-height: 50px;
    text-align: right;
    .title {
      float: left;
      font-size: 14px;
      i {
        vertical-align: middle;
        font-size: 20px;
        margin-right: 5px;
      }
    }
  }
  .wrapper {
    height: calc(100% - 65px);
    overflow: hidden;
    width: 100%;
    padding: 30px 0;
    .ivu-card {
      width: 50%;
      margin: auto;
      max-height: calc(100% - 60px);
      overflow: auto;
      .ivu-tree {
        text-align: left;
        /*overflow: auto;*/
        .ivu-tree-arrow {
          float: left !important;
          margin-right: 10px;
        }
        .ivu-tree-children {
          padding: 0 0 0 40px;
        }
      }
    }
  }
}
