



















































































































































.rightContent {
  width: calc(100% - 22px);
  display: inline-block;
  .btns {
    margin-left: 10%;
    text-align: center;
    padding-left: 10px;
    display: none;
  }
  &:hover {
    .btns {
      display: inline-block;
      .ivu-icon {
        font-size: 14px;
      }
    }
  }
}
.btn-add {
  background-color: #01c296;
  border-color: #01c296;
  &:hover, &:focus {
    background-color: #05ab85;
    border-color: #05ab85;
  }
}
